(function () {

///////////////////////////////////////////////////////////////////////
//                                                                   //
// packages/clinical:auto-resizing/lib/resizing.js                   //
//                                                                   //
///////////////////////////////////////////////////////////////////////
                                                                     //
Session.setDefault('resize', new Date());                            // 1
Session.setDefault('appHeight', $(window).height());                 // 2
Session.setDefault('appWidth', $(window).width());                   // 3
                                                                     // 4
Meteor.startup(function () {                                         // 5
  window.addEventListener('resize', function () {                    // 6
    Session.set("resize", new Date());                               // 7
    Session.set("appHeight", $(window).height());                    // 8
    Session.set("appWidth", $(window).width());                      // 9
  });                                                                // 10
});                                                                  // 11
                                                                     // 12
                                                                     // 13
Template.registerHelper('resize', function () {                      // 14
  return Session.get('resize');                                      // 15
});                                                                  // 16
Template.registerHelper('isPortrait', function () {                  // 17
  if (Session.get('appHeight') > Session.get('appWidth')) {          // 18
    return true;                                                     // 19
  } else {                                                           // 20
    return false;                                                    // 21
  }                                                                  // 22
});                                                                  // 23
Template.registerHelper('isLandscape', function () {                 // 24
  if (Session.get('appHeight') < Session.get('appWidth')) {          // 25
    return true;                                                     // 26
  } else {                                                           // 27
    return false;                                                    // 28
  }                                                                  // 29
});                                                                  // 30
                                                                     // 31
///////////////////////////////////////////////////////////////////////

}).call(this);
